import React from "react";

const ProjectCardCustom = ({value}) => {

    const {
        heading,
        description,
        languages,
        updateDate
    } = value;

    return (
        <div className="col-md-6">
            <div className="card p-3 mb-5 shadow-lg bg-white rounded">
                <div className="card-body">
                    <div className="card-title h3">{heading}</div>
                    <div className="card-text">{description}</div>          
                    <hr />
                    <Language languages={languages}/>
                    <CardFooter updateDate={updateDate}/> 
                </div>
            </div>
        </div>
    )
}

const Language = ({ languages }) => {
   
   const array = [];
   for (let x in languages){
    array.push(languages[x])
   }

    return (
        <div className="pb-3">
            Languages:{" "}
            {array.map((language)=>
                (
                    <div className="badge badge-light card-link" key={language} >{language}</div>
                )
            )}
        </div> 
    );
};

const CardFooter = ({  updateDate }) => {

    return (
        <p className="card-text">
            <small className="text-muted">Updated {updateDate}</small>
        </p>
    )
};

export default ProjectCardCustom;